<template>
  <div id="login-component">
    <el-card
      class="card"
      :body-style="{
        padding: '20px 50px'
      }"
    >
      <div class="title">Login</div>
      <div class="main">
        <div class="item">
          <p>Username</p>
          <el-input v-model="form.username"></el-input>
        </div>
        <div class="item" :class="{ error: isPasswordError }">
          <p>Password</p>
          <el-input
            type="password"
            v-model="form.password"
            @keyup.enter="onLoginClick"
          ></el-input>
        </div>
      </div>
      <div class="footer">
        <el-button type="success" @click="onLoginClick">Login</el-button>
      </div>
    </el-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import { LoginParams } from "@/apis/auth";
import { Login } from "@/typings/store";

const Component = defineComponent({
  name: "Login",
  setup() {
    const store = useStore();
    const router = useRouter();

    const form = reactive<LoginParams>({
      username: "",
      password: ""
      // username: "littleking",
      // password: "netdbat02777",
      // username: "nike",
      // password: "nike@02711"
    });
    const isPasswordError = ref(false);

    const onLoginClick = async () => {
      const isPass = await store.dispatch(Login, form);
      if (isPass) {
        await router.push("/");
        location.reload();
      } else {
        isPasswordError.value = true;
      }
    };

    return {
      form,
      isPasswordError,

      onLoginClick
    };
  }
});

export default Component;
</script>

